* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-info__header {
  font-family: serif;
  font-size: 37px;
  display: flex;
  gap: 10px;
}

.country-info__item{
  font-family: serif;
  font-size: 25px;
  display: flex;
  gap: 10px;
}

.country-list{
  list-style: none;
  padding-left: 5px;
  
}

.country-list__item{
  font-family: serif;
  font-size: 25px;
  display: flex;
  gap: 10px;
}